<template>
  <div>
    <headerBar :header-data="headerData" />
    <div class="container">
      <div class="content">
        <div class="common-main box">
          <div class="notice-title">{{ ggxq.bt }}</div>
          <div class="notice-date">
            <span>
              <i class="iconfont iconjingbanren" />
              {{ ggxq.fbrxm }}
            </span>
            <span>
              <i class="iconfont iconshijian" />
              {{ ggxq.fbsj | formatTime12 }}
            </span>
          </div>

          <div class="notice-content">
            <div v-html="ggxq.nr" />
          </div>
        </div>
      </div>

      <!-- 附件列表 begin -->
      <div class="content">
        <div class="common-main box">
          <!-- 通用标题 beign -->
          <div class="title">
            <div class="title-left">
              <h2>附件列表</h2>
            </div>
          </div>
          <!-- 通用标题 end -->

          <div class="attachment-list">
            <dl v-for="(item,index) of ggxq.ggfjArr" :key="index">
              <dt>
                <i class="iconfont iconfujian" />
              </dt>
              <dd @click="loadFiles(item.xzlj)">{{ item.fjmc }}</dd>
            </dl>
          </div>
        </div>
      </div>
      <!-- 附件列表 end -->

    </div>
    <Backtop />
  </div>
</template>

<script land="script" scoped>
import constants from '../../../common/js/constants'
import { formatTimes } from '../../../common/js/util'
import { mapState, mapActions } from 'vuex'
import { pageRouter, NativeJS } from '@/common/js/mixin.js'
import headerBar from '@/components/header/headerBar'
import Backtop from '@/components/backtop/backtop' // 返回顶部
// import { getYqhqrAction } from '../../../store/gys/wybm'
// import popYqhck from '../../../components/popup/pop-yqhck'
export default {
  name: 'XtggGgxq',
  components: {
    headerBar,
    Backtop
  },
  mixins: [pageRouter, NativeJS],
  data() {
    return {
      headerData: {
        title: '公告详情',
        leftUrl: 'back',
        leftText: ' '
      },
      wybmListItem: {
      },

      status: {
        isShowPublish: false,
        topNum: 30
      },
      ggxq: '',
      id: ''
    }
  },
  computed: {
    ...mapState({
      // userInfo: state => state.user.userInfo,
      // wybmListItem: state => state.wybm.wybmListItem,
      // gysyqk: state => state.wybm.yqhck
    })
  },
  created() {
    this.id = this.$route.params.id
    this.XTGGZT = constants.XTGGZT
    this.getGgxq()
    // this.wybmListItem = this.$route.params.data
    // this.wybmListItem.bt = this.$route.params.data.bt
    // this.wybmListItem.zt = this.$route.params.data.zt
    // this.wybmListItem.cjsj = formatTimes(this.$route.params.data.cjsj)
    // this.wybmListItem.fbsj = formatTimes(this.$route.params.data.fbsj)
    // this.wybmListItem.dwmc = this.$route.params.data.dwmc
    // this.wybmListItem.fbrxm = this.$route.params.data.fbrxm
  },
  methods: {
    loadFiles(url) {
      this.NativeDownFiles(url)
    },
    getGgxq() {
      this.$store.dispatch('erp/getXtggByIdAction', {
        id: this.id
      }).then(res => {
        this.ggxq = res
      }).catch(error => {
        this.$toast(error)
      })
    },
    ...mapActions([
      // 'wybmfbAction'
    ]),
    getTime(data) {
      var time = formatTimes(data)
      return time
    },
    closeDialog() {
      if (this.gysyqk !== null && this.gysyqk !== 0) {
        this.status.isShowPublish = false
      } else {
        this.$toast.fail('暂无数据！')
      }
    }
    // yqhckAction() {
    //   this.status.isShowPublish = true
    //   console.log('===SET_GO_WYBM========')
    //   // this.SET_GO_WYBM('true')
    // }
  }
}
</script>

